.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.brandIcon {
  width: 25px;
  height: 25px;
  display:inline-block;
  margin-right: 5px; 
  vertical-align: -25%;
  color: #377dFF;
}

.bar-btn {
  background-color: white;
  border: 1px solid #4E5D78;
  border-radius: 5px;
  padding: 5px 10px 5px 10px;
  color: #4E5D78;
  font-weight: 600;
  box-shadow: 2px 2px 5px 1px #dedfe0;
  &:hover {
      cursor: pointer;
      background-color: white;
      color: #377dFF;
      border: 1px solid #377dFF;
  }
  
}


.bar-user-id {
  color: #377dFF;
  margin-right: 10px;
}