

@font-face {
    font-family: 'S-CoreDream-3Light';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/S-CoreDream-3Light.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}


@font-face {
    font-family: 'NEXON Lv2 Gothic';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-04@2.1/NEXON Lv2 Gothic.woff') format('woff');
    font-style: normal;
}


* {
    font-family: 'NEXON Lv2 Gothic';
}


.center-align-col {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.center-align-row {
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
}

.padding10px {
    padding: 10px;
}

.login-container {
    padding: 20px;
    box-shadow: 5px 1px 20px 5px #f6f7f8;
}


/* log in with ~~~ */

.login-with-btn {
    background-color: #f6f7f8;
    border: 1px solid white;
	border-radius: 5px;
    padding: 10px 20px 10px 20px;
    color: #4E5D78;
    font-weight: 600;
    margin: 10px;
    height: 40px;
    &:hover {
        cursor: pointer;
        background-color: white;
        color: #377dFF;
        border: 1px solid #377dFF;
    }
}

/* https://nuggy875.tistory.com/115 */

.big-icon {
    width: 15px;
    height: 15px;
    display:inline-block;
    margin-right: 10px; 
    vertical-align: -25%;
}

.line-width{
    width: 180px;
}

.bold-gray-text {
    font-weight: bold;
    color: #4E5D78;
}

.bold-blue-text {
    font-weight: bold;
    color: #377dFF;
}

.gray {
    color: #4E5D78;
}

.blue {
    color: #377dFF;
}


/* 입력창 */

.userInputs {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    padding:20px
}

.inputbox {
    border: solid 1px #eaebec;
	border-radius: 5px;
    padding: 10px 15px 10px 20px;
    width: 320px;
    display: flex;
    &:focus-within {outline: solid 1px #377dFF;} /* outline 테두리 없애기 */
}

.input {
    border:none;
    flex: 1;
    &:focus {outline: none;} /* outline 테두리 없애기 */
}

.checkbox {
    width: 360px;
    display: flex;
    padding-top: 15px;
}

.flex1 {
    flex: 1;
}


/* 로그인 버튼 */
.login-btn {
    background-color: #377dFF;
    color: white;
    border:none;
	border-radius: 5px;
    padding: 10px 20px 10px 20px;
    margin: 10px;
    width: 90%;
    height: 40px;

    &:hover {
        cursor: pointer;
        background-color: white;
        color: #377dFF;
        border: 1px solid #377dFF;
    }
}

.pointer:hover {
    cursor: pointer;
    font-weight: bold;
}