
.red {
    color: red;
}

.gray {
  color: #4E5D78;
}

.blue {
  color: #377dFF;
}


/* 모달 */
.bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.6);
    
    display: flex;
    justify-content: center;
    align-items: center;
  }

.modalBox {
    background-color: #fff;
    width: 600px;
    height: auto;
    padding: 15px;
    text-align: right;
    border-radius: 10px;
  }

.modalBox button {
    margin-bottom: 15px;
  }

.inputZipCode {
    border: solid 1px #eaebec;
    border-radius: 5px;
    padding: 10px 15px 10px 20px;
    margin-top: 10px;
    width: 80px;
    &:focus-within {outline: solid 1px #377dFF;} /* outline 테두리 없애기 */
  }

.inputAddress {
    border: solid 1px #eaebec;
    border-radius: 5px;
    padding: 10px 15px 10px 20px;
    width: 350px;
    margin-top: 5px;
    &:focus-within {outline: solid 1px #377dFF;} /* outline 테두리 없애기 */
  }


  /* 버튼 */

  .open-btn {
    margin: 5px;
    background-color: #8891a1;
    border: 1px solid white;
    border-radius: 5px;
    padding: 9px;
    color:white;
    &:hover {
        cursor: pointer;
        background-color: white;
        color: #377dFF;
        border: 1px solid #377dFF;
    }
    
  }
  
.signup-container {
  padding: 20px;
  box-shadow: 5px 1px 20px 5px #f6f7f8;
}

/* 인풋박스 */

.signup-inputbox {
    border: solid 1px #eaebec;
	  border-radius: 5px;
    padding: 10px 15px 10px 20px;
    width: 350px;
    display: flex;    
    margin-top: 10px;
    margin-bottom: 10px;
    &:focus-within {outline: solid 1px #377dFF;} /* outline 테두리 없애기 */
}

.signup-input {
    border:none;
    flex: 1;
    &:focus {outline: none;}  /* outline 테두리 없애기 */
}

.category {
  margin: 20px;
}


/* 회원가입 버튼 */
.signup-btn {
  background-color: #377dFF;
  color: white;
  border:none;
  border-radius: 5px;
  padding: 10px 20px 10px 20px;
  margin: 10px;
  width: 95%;
  height: 40px;

  &:hover {
      cursor: pointer;
      background-color: white;
      color: #377dFF;
      border: 1px solid #377dFF;
  }
}


.margin_vertical {
  margin-top: 10px;
  margin-bottom: 10px;
}

.chkText {
  font-size: small;
  padding-left: 10px;
}